/* Home styling */

button{
  padding: 10px 30px;
  border-radius: 10px;
  border: none;
  /* margin: 0 20px; */
  background: rgba(0,0,0,0.75);
  /* text-transform: uppercase; */
  /* font-size: 16px; */
  letter-spacing: 1.5px;
  /* background: #545152; */
  color: white;
  
  transition: all 0.7s;
  /* border: 1px dotted #cdc4b9; */
}

button:hover{
  transform: scale(1.1);
}

.homeButton {

  margin: 20px;
}

