/* List styling */

.relatedWords{
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin: 20px 0;
  padding: 0;
}

.relatedWords li{
  list-style: none;
  /* width: 20%; */
}

.relatedWords button{
  /* width: 100%; */
  background: rgba(0,0,0,0.5);
  border: none;
  color: white;
}

.printedHaiku{
  color: black;
  min-height: 100vh;
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}


.autoCompleteSuggestions {
  background-color: #cdc4b9;
}

.underline {
  font-style: italic;
  color: #545152;
}

.autoCompleteSuggestions button {
  background-color: transparent;
  color: #545152;
  transition: all 0.3s;
  width: 100%;
  height: 100%;
  text-align: left;
}

.autoCompleteSuggestions button:focus,
.autoCompleteSuggestions button:hover {
  transform: none;
  color: white;
}

.autoCompleteSuggestions li:focus,
.autoCompleteSuggestions li:hover {
  background-color: #545152;
  color: white;
}


